const app = getApp()
import cookies from '../../vendor/weapp-cookie/dist/weapp-cookie'
import Authorization from "../../utils/authorize.js"
import cookieUtil from "../../utils/cookie.js"
export default {
	data() {
		return {
			gridList: [{
				text: "我的收藏",
				src: "../../static/collection.png",
				status: false
			},
			{
				text: "我的工单",
				src: "../../static/workorder.png",
				status: false
			},
			{
				text: "我的约伴",
				src: "../../static/friends.png",
				status: false
			}],
			pageList: [{
				text: "身份认证",
				src: "/static/auth.png",
				openType: "",
			},
			{
				text: "关于我们",
				src: "/static/about.png",
				openType: ""
			},{
				text: "推荐给好友",
				src: "/static/share.png",
				openType: "share"
			}],
			userInfo: null,
			tip: "点击授权",
		}
	},
	onShow() {
		console.log("onshow")
		console.log(app.globalData.userInfo)
		if(app.globalData.userInfo){
			console.log(app.globalData.userInfo)
			this.userInfo = app.globalData.userInfo
		}
	},
	onLoad() {
		console.log("onload")
		// this.getUserInfoBySetting()
		// console.log(app.globalData.userInfo)
	},
	methods: {
		/*列表点击事件*/
		// 点击跳转或者授权
		listClick(items, index){
			this.navigato(index)
		},
		// 跳转页面
		navigato(index){
			if(index==0){
				// 已经验证过的不可验证
				if(this.userInfo.is_auth){
					uni.showModal({
						title: '您已经验证过',
						content: "不要重复验证哟",
						confirmText: '知道了',
						showCancel: false
						
					})
				}else{
					this.loadingAndNavigato("../authentication/authentication")
				}
			}
			if(index==1){
				this.loadingAndNavigato("../about/about")
			}
		},
		// 页面加载和跳转
		loadingAndNavigato(src){
			uni.showLoading({
				title: "跳转中",
				success() {
					uni.navigateTo({
						url: src
					})
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		/*九宫格点击事件*/
		// 按钮变色
		changeColor(bool, items, index){
			var that = this
			var gridList = that.gridList
			gridList[index].status = bool
			that.gridList = gridList
		},
		// 待开发提示
		waitDev(){
			uni.showToast({
				title: "功能尚待开发"
			})
		},
		// 用户松开点击，跳转
		gridEndClick(items, index){
			// 按钮变回白色
			this.changeColor(false, items, index)
			// 页面跳转（功能尚待开发）
			this.waitDev()
		},
		// 用户点击态
		gridClick(items, index){
			// 按钮灰变色
			this.changeColor(true, items, index)
		},
		
		// 首先获取用户信息设置到globalData，然后携带信息请求服务端登陆接口并且将登录状态设置为缓存
		onGotUserInfo(e) {
			var that = this
			uni.showLoading({
				title: "登录中"
			})
		    // console.log(e.detail.userInfo)
		    var userInfo = e.detail.userInfo
			// that.userInfo = app.globalData.userInfo
			that.setCookie(app, userInfo)
		},
		// 获取code携带userInfo同步向服务端发起登录请求，并且将cookie设置为缓存
		setCookie: async function (app, userInfo) {
			var result = await Authorization.login(app, userInfo)
			console.log(result)
			// 如果登陆成功
			if(result){
				console.log(result.data.data.openid)
				// 将openid存入缓存
				uni.setStorageSync('openid', result.data.data.openid)
				app.globalData.userInfo = result.data.data
				this.userInfo = app.globalData.userInfo
				uni.hideLoading()
				uni.showToast({
					title: '登陆成功'
				})
			}else{
				this.userInfo = null
				uni.hideLoading()
				uni.showModal({
					title: "登录错误",
					content: "登录失败，请重新点击登陆",
					showCancel: false,
					confirmText: '确定'
				})
			}
		}
	}
}
